}

.concept {
  position: relative;
  // width: fit-content;
  box-sizing: border-box;
  // border: 1px solid black;

  &:hover::before {
    margin-top: -6.8cm;

    opacity: 1;
  }
}

.concept::before {
  position: absolute;
  content: "";
  z-index: 10;
  background-image: url("conceptl.gif");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: 6cm auto;
  // margin-top: -6.2cm;
  opacity: 0;
  inset: 0;
  // transition: opacity 0.2s ease;
