.cats {
  display: flex;
  justify-content: center;

  margin-top: .7cm;
  column-gap: .5cm;
  row-gap: .5cm;
  flex-wrap: wrap;





}



.category {
  width: 5cm;
  padding: .3cm .6cm .3cm .6cm;
  cursor: pointer;
  background-color: white;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  row-gap: .25cm;

  box-shadow: rgba(9, 30, 66, 0.25) 0px 4px 8px -2px, rgba(9, 30, 66, 0.08) 0px 0px 0px 1px;
  
  justify-content: space-around;
  transition: .07s all ease-in-out;
  position: relative;

  &:hover {
    .catImg {
      transform: scale(1.035);
    }

  }


  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    background-image: url("bacc.jpg");
    background-repeat: no-repeat;
    background-size: auto 100%;
    background-position: center center;
    // background-color: #47286b;
    opacity: .075;

  }


}




.drp {
  position: relative;

  &:hover {
    .drpdwn {
      display: flex;
    }
  }
}

.drpdwn {
  display: none;
  // display: flex;
  margin-left: -.5cm;
  margin-top: .05cm;
  padding: .2cm .3cm;
  background-color: rgb(238, 219, 255) transparent;
  backdrop-filter: blur(5px);
  position: absolute;
  z-index: 100;
  border: 2px solid rgb(219, 178, 255);
  border-radius: 10px;
  flex-direction: column;
  row-gap: .5cm;
  align-items: center;
}


.drpItemHome {
  color: white;
}




.pr {
  position: relative;
  transform: translateX(-150px);
  opacity: 0;
  transition: .7s all ease;
}

.pr.active {
  transform: translateX(0);
  opacity: 1;
}

.ct {
  position: relative;
  transform: translateY(100px);
  opacity: 0;
  transition: .7s all ease;
}

.ct.active {
  transform: translateY(0);
  opacity: 1;
}


.ht {
  position: relative;
  transform: scale(0);
  opacity: 0;
  transition: .75s all ease;
}

.ht.active {
  transform: scale(1);
  opacity: 1;
}






